<template>
  <div class="invalid full-screen">
    <div class="empty-result">
      <div class="empty-result-icon icon-error"></div>
      <span>Oh , 链接失效了...</span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import { setTitle } from 'common/js/util'

export default {
  created () {
    this._setTitle()
  },
  methods: {
    _setTitle () {
      setTitle('错误')
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .invalid
    font-size: 0.24rem
    background-color: $color-panel
    width:7.5rem
    .empty-result
      font-size: 0
      text-align: center
      padding-top: 3.36rem
      .empty-result-icon
        display: inline-block
        width: 4.14rem
        height: 4.14rem
        background-size: 4.14rem 4.14rem
        margin-bottom: 0.34rem
        &.icon-error
          bg-image('icon-Invalid')
      span
        display: block
        font-size: 0.4rem
        color: #a7a7a7
        line-height: 0.56rem
        letter-spacing: -0.0094rem
</style>



